#login-page {
  height: 100vh;
  margin: 0;
  padding: 0;
  background-image: url("../images/dog-pattern_2.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat; 
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  
}
  
.welcome-box {
  background-color: #ececec;
  padding: 15px;
  height: 30vh;
  border-radius: 25px;
  margin-right: 20%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  text-align: center;
}

.btns-container {
  height: 40%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.display-none {
  display: none;
}

.modal-text {
  margin-top: 15px;
  margin-bottom: 10px
}

.modal-btn {
  margin-bottom: 15px;
}

